a {
  display: block;
  width: 80%;
  text-decoration: none;
  text-align: center;
  padding: 1.7rem 1rem;
  margin: 0 auto;
  color: #000;
  font-size: 1.5rem;
  max-width: 370px;
}

#id_00_00 a {
  background-color: red;
  transition: 300ms;
}

#id_00_00 a:hover {
  background-color: blue;
}

#id_00_01 a {
  opacity: 1;
  transition: 300ms;
  background-color: skyblue;
}

#id_00_01 a:hover {
  opacity: .5;
}

#id_00_02 a {
  background-color: red;
  transition: 300ms;
}

#id_00_02 a:hover {
  background-color: rgba(255, 0, 0, 0.5);
}

#id_00_03 a {
  background-image: linear-gradient(to right, transparent 50%, #001e4b 50%);
  background-size: 210% auto;
  background-position: 0 0;
  transition: background 300ms ease 0ms, color      300ms ease 300ms;
}

#id_00_03 a:hover {
  background-position: -95% 0;
  color: #fff;
}

#id_00_04 a {
  background: repeating-linear-gradient(-45deg, #444, #444 2%, #888 0, #888 4%);
  background-size: 100% auto;
  transition: 450ms;
  background-position: 0 0;
}

#id_00_04 a:hover {
  background-position: 44.25% 0;
  background-size: 5000% auto;
}

#id_00_05 a {
  border: 2px solid  blue;
  background-image: linear-gradient(to right, transparent 50%, blue 50%);
  background-size: 210% auto;
  background-position: 97% 0;
  transition: 300ms;
}

#id_00_05 a:hover {
  background-position: 0 0;
}

#id_00_06 a {
  background-image: url("https://unsplash.it/300/50/?gravity=east");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100% auto;
  transition: 300ms;
}

#id_00_06 a:hover {
  background-size: 105% auto;
}

#id_00_07 p {
  overflow: hidden;
}

#id_00_07 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_07 a:hover {
  filter: blur(2px);
}

#id_00_08 p {
  overflow: hidden;
}

#id_00_08 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_08 a:hover {
  filter: invert(75%);
}

#id_00_09 p {
  overflow: hidden;
}

#id_00_09 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_09 a:hover {
  filter: contrast(150%);
}

#id_00_10 p {
  overflow: hidden;
}

#id_00_10 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_10 a:hover {
  filter: brightness(1.2);
}

#id_00_11 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_11 a:hover {
  filter: grayscale(50%);
}

#id_00_12 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_12 a:hover {
  filter: hue-rotate(150deg);
}

#id_00_13 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_13 a:hover {
  filter: sepia(60%);
}

#id_00_14 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_14 a:hover {
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.3);
}

#id_00_15 a {
  background: url("https://unsplash.it/300/50/?gravity=east") center center no-repeat;
  background-size: cover;
  transition: 300ms;
}

#id_00_15 a:hover {
  box-shadow: inset 0 0 5px 5px rgba(0, 0, 0, 0.3);
}

#id_00_16 a {
  text-shadow: 0 0 0 rgba(0, 0, 0, 0.5);
  transition: 300ms;
}

#id_00_16 a:hover {
  text-shadow: 10px 10px 0 rgba(0, 0, 0, 0.5);
}

#id_00_17 a {
  text-shadow: 0 0 0 rgba(0, 0, 0, 0.5);
  transition: 300ms;
}

#id_00_17 a:hover {
  color: #fff;
  text-shadow: 4px 0 8px rgba(0, 0, 0, 0.5), 4px 4px 8px rgba(0, 0, 0, 0.5), 0 4px 8px rgba(0, 0, 0, 0.5), -4px 4px 8px rgba(0, 0, 0, 0.5), -4px 0 8px rgba(0, 0, 0, 0.5), -4px -4px 8px rgba(0, 0, 0, 0.5), 0 -4px 8px rgba(0, 0, 0, 0.5), -4px 0 8px rgba(0, 0, 0, 0.5);
}

#id_00_18 a {
  border: 1px solid  black;
  border-bottom-width: 3px;
}

#id_00_18 a:hover {
  transform: translateY(3px);
  margin-top: 2px;
  border-bottom-width: 1px;
}

#id_00_19 a {
  border: 1px solid  #333;
  transition: 300ms;
}

#id_00_19 a:hover {
  border-radius: 25px;
}

#id_00_20 a {
  letter-spacing: 0.1em;
  transition: 300ms;
}

#id_00_20 a:hover {
  letter-spacing: 1em;
  text-indent: 1em;
}
